$primary-gray-color: #454b54; 
$white-color: #fff; 

.klarnaBtn{
    background-color: $primary-gray-color;
    border:none;
    color: $white-color;
    font-size: 14px;
    font-weight: bold;
    padding: 10px;
    text-transform: uppercase;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    cursor: pointer;
}

.klarnaBtn svg{
    margin-right: 10px;
}

.authorizeBtn{
    background-color: $white-color;
    border: 1px solid $primary-gray-color;
    color: $primary-gray-color;
    font-size: 14px;
    font-weight: bold;
    padding: 10px;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
}