$primary-gray-color: #bd111f; 
$white-color: #fff; 

.stripeBtn{
    background-color: $primary-gray-color;
    border:none;
    color: $white-color;
    font-size: 14px;
    font-weight: bold;
    padding: 10px;
    text-transform: uppercase;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    cursor: pointer;
}

.stripeBtn svg{
    margin-right: 10px;
}
